import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Issues.css';
import classnames from 'classnames';
import Anchor from '../Anchor';

import imgIssues from './issues.png';

class Issues extends React.Component {
  render() {
    return (
        <div style={{backgroundColor: this.props.bgcolor}} className={s.root}>
            <Anchor name={'issue'}/>
            <div className={s.sectionBackground}>
                <div className={classnames(s.row, s.center_xs)}>
                    <div className={s.col_xs_8}>
                        &nbsp;
                    </div>
                </div>
            </div>
            <div className={s.section}>
                <div className={classnames(s.row, s.center_xs)}>
                    <div className={s.col_xs_8}>
                        <div className={s.square}></div>
                        <h3 className={s.title}>{ this.props.title }</h3>
                    </div>
                </div>
            </div>

            <div>
                <div className={classnames(s.row, s.center_xs)}>
                    <div className={s.col_xs_8}>
                        <p className={s.subtitle}>
                            The Connected World needs an identity system that support the verification of humans (KYC), object (KYO) and connected devices (KYD)
                        </p>
                    </div>
                </div>

                <div className={classnames(s.row, s.center_md, s.center_xs)}>
                    <div className={classnames(s.col_lg_4,s.col_md_10, s.col_xs_10)}>
                        <img className={s.image} src={imgIssues} srcSet={`${imgIssues} 2x`} alt='How BlockPass work' />
                    </div>
                    <div className={classnames(s.col_lg_4,s.col_md_10, s.col_xs_10, s.content)}>
                        <p>Identity verification requires each time an individual access
                            for a new product or service, resulting in a slow and painful
                            process that risks to reduce conversions.</p>
                        <p>Service Providers spend time and money developing internal
                             KYC solutions and they’re exposed to compliance risk and
                             100% assumed liability.</p>
                         <p>IoT device vulnerability to hacks and device thefts may
                              compromise data integrity and manipulation of confidential
                              informations and participation in DDoS attacks.</p>
                          <p>Identity verification on networks is exposed to cyber attacks,
                              ransomware, identity fraud and because of an increasingly
                              stringent regulatory environment it is becoming
                              harder and pricier for businesses to comply
                              quickly and effectively.</p>
                    </div>
                </div>
            </div>
        </div>
    );
  }
}

export default withStyles(s)(Issues);



