import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Partners.css';
import classnames from 'classnames';
import Anchor from '../Anchor';

import ibl from './logos/IBL.png';
import cot from './logos/cot.png';
import coinfirm from './logos/coinfirm.png';
import diacle from './logos/diacle.png';

class Partners extends React.Component {

    render() {
      return (
          <div style={{backgroundColor: this.props.bgcolor}} className={s.root}>
              <Anchor name={'partners'}/>
              <div className={s.section}>
                  <div className={classnames(s.row, s.center_xs)}>
                      <div className={s.col_xs_8}>
                          <div className={s.square}></div>
                          <h3 className={s.title}>{ this.props.title }</h3>
                      </div>
                  </div>
              </div>

              <div>
                  <div className={classnames(s.row, s.center_xs)}>
                      <div className={classnames(s.col_lg_2,s.col_md_2, s.col_xs_10, s.vertAligned)}>
                        <a className={s.brand} href="https://www.chainofthings.com/">
                          <img className={s.logo} src={cot} srcSet={`${cot} 2x`} alt='Chain of Things' />
                        </a>
                      </div>
                      <div className={classnames(s.col_lg_2,s.col_md_2, s.col_xs_10, s.vertAligned)}>
                        <a className={s.brand} href="http://www.blockchainlabs.asia/">
                          <img className={s.logo} src={ibl} srcSet={`${ibl} 2x`} alt='Infinity Blockchain Labs' />
                        </a>
                      </div>
                      <div className={classnames(s.col_lg_2,s.col_md_2, s.col_xs_10, s.vertAligned)}>
                        <a className={s.brand} href="https://www.coinfirm.io/">
                          <img className={s.logo} src={coinfirm} srcSet={`${coinfirm} 2x`} alt='Coinfirm' />
                        </a>
                      </div>
                      <div className={classnames(s.col_lg_2,s.col_md_2, s.col_xs_10)}>
                        <a className={s.brand} href="https://www.site.diacle.com/">
                          <img className={s.logo} src={diacle} srcSet={`${diacle} 2x`} alt='Diacle' />
                        </a>
                      </div>

                  </div>

                  <div className={classnames(s.row, s.center_md, s.center_xs)}>
                  </div>
              </div>
          </div>
      );
    }
}


export default withStyles(s)(Partners);



