import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Mission.css';
import classnames from 'classnames';
import Anchor from '../Anchor';

import imgMission from './mission.png';

class Mission extends React.Component {
  render() {
    return (
        <div style={{backgroundColor: this.props.bgcolor}} className={s.root}>
            <Anchor name={'mission'}/>
            <div className={s.section}>
                <div className={classnames(s.row, s.center_xs)}>
                    <div className={s.col_xs_8}>
                        <div className={s.square}></div>
                        <h3 className={s.title}>{ this.props.title }</h3>
                    </div>
                </div>
            </div>

            <div>
                <div className={classnames(s.row, s.center_xs)}>
                    <div className={s.col_xs_8}>
                        <p className={s.subtitle}>
                            The goal of Blockpass is global realization of identity for the Internet of Everything
                        </p>
                    </div>
                    <div className={s.col_xs_6}>
                        <p>
                            Through the use of blockchain technology and smart contracts,
                            Blockpass is a production ready Regtech platform offering shared
                            regulatory and compliance services for humans, companies, objects
                            and devices. <br />
                            This will enable the development of new applications
                            that rely on a trusted connection between human and device identities.
                        </p>
                    </div>
                </div>

                <div className={classnames(s.row, s.center_md, s.center_xs)}>
                    <div className={classnames(s.col_lg_4,s.col_md_10, s.col_xs_10, s.content)}>
                        <p>Service provider cost reduction when on-boarding new customers</p>
                        <p>Customer inconvenience reduction when signing up for multiple blockchain service providers</p>
                         <p>Allow individuals to transfer ownership, share or permission physical devices and objects</p>
                          <p>Permit connected devices to transmit data into the blockchain as trusted Oracles</p>
                          <p>Enable dynamic interaction with connected devices and Blockpass users</p>
                    </div>
                    <div className={classnames(s.col_lg_4,s.col_md_10, s.col_xs_10)}>
                        <img className={s.image} src={imgMission} srcSet={`${imgMission} 2x`} alt='How BlockPass work' />
                    </div>

                </div>
            </div>
        </div>
    );
  }
}

export default withStyles(s)(Mission);



