import React, {PropTypes} from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './HowDoes.css';
import classnames from 'classnames';
import FontAwesome from 'react-fontawesome';
import Anchor from '../Anchor';

import imgMechanism from './howdoesitwork.png';


class HowDoes extends React.Component {
  static propTypes = {
    title: PropTypes.string.isRequired,
    bgcolor: PropTypes.string.isRequired,
    textcolor: PropTypes.string.isRequired
  }

  render() {
    return (
      <div style={{backgroundColor: this.props.bgcolor}} className={s.root}>
          <Anchor name={'concept'}/>
          <div className={s.section}>
              <div className={classnames(s.row, s.center_xs)}>
                  <div className={s.col_xs_8}>
                      <div className={s.square}></div>
                      <h3 className={s.title}>{ this.props.title }</h3>
                  </div>
              </div>
          </div>

        <div className={classnames(s.sectionBackground)}>
            <div className={classnames(s.row, s.center_xs)}>
                <div className={s.col_xs_8}>
                    <p className={s.subtitle}>Create an identity for humans, objects and devices</p>
                </div>
            </div>

          <div className={classnames(s.row, s.center_md, s.center_xs)}>
            <div className={classnames(s.col_lg_4,s.col_md_10, s.col_xs_10,s.blueBackground)}>
                <p className={s.step}><span>1</span> Download Blockpass app</p>
                <p className={s.step}><span>2</span> Complete the KYC process</p>
                <p className={s.step}><span>3</span> Receive a blockpass verified identity</p>
                <p className={s.step}><span>4</span> Interact with Blockpass</p>
                <p className={s.substep}> connect service</p>
                <p className={s.substep}> create object identities</p>
                <p className={s.substep}> create device identities</p>
            </div>
            <div className={classnames(s.col_lg_4,s.col_md_10, s.col_xs_10)}>
                <img className={s.image} src={imgMechanism} srcSet={`${imgMechanism} 2x`} alt='How BlockPass work' />
            </div>
          </div>

          <div className={classnames(s.row, s.center_xs)}>
            <div className={s.col_xs_8}>
                <h3>&nbsp;</h3>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(HowDoes);



