import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import Paper from 'material-ui/Paper';
import s from './Team.css';
import classnames from 'classnames';
import Anchor from '../Anchor';

class Team extends React.Component {

    render() {
      return (
          <div style={{backgroundColor: this.props.bgcolor}} className={s.root}>
              <Anchor name={'team'}/>
              <div className={s.section}>
                  <div className={classnames(s.row, s.center_xs)}>
                      <div className={s.col_xs_8}>
                          <div className={s.square}></div>
                          <h3 className={s.title}>{ this.props.title }</h3>
                      </div>
                  </div>
              </div>

              <div>
                  <div className={classnames(s.row, s.center_xs)}>
                      <div className={classnames(s.col_lg_2,s.col_md_2, s.col_xs_10)}>
                          <div className={s.avatar}></div>
                          <div className={s.name}>AV</div>
                          <div className={s.position}>CEO</div>
                      </div>
                      <div className={classnames(s.col_lg_2,s.col_md_2, s.col_xs_10)}>
                          <div className={s.avatar}></div>
                          <div className={s.name}>HL</div>
                          <div className={s.position}>CMO</div>
                      </div>
                      <div className={classnames(s.col_lg_2,s.col_md_2, s.col_xs_10)}>
                          <div className={s.avatar}></div>
                          <div className={s.name}>TL</div>
                          <div className={s.position}>CTO</div>
                      </div>
                      <div className={classnames(s.col_lg_2,s.col_md_2, s.col_xs_10)}>
                          <div className={s.avatar}></div>
                          <div className={s.name}>TL</div>
                          <div className={s.position}>CTO</div>
                      </div>
                      <div className={classnames(s.col_lg_2,s.col_md_2, s.col_xs_10)}>
                          <div className={s.avatar}></div>
                          <div className={s.name}>TL</div>
                          <div className={s.position}>CTO</div>
                      </div>
                  </div>

                  <div className={classnames(s.row, s.center_md, s.center_xs)}>
                  </div>
              </div>
          </div>
      );
    }
}


export default withStyles(s)(Team);



