import React from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Navigation.css';

import Scroll from 'react-scroll';

let Link       = Scroll.Link;
let Element    = Scroll.Element;
let Events     = Scroll.Events;
let scroll     = Scroll.animateScroll;
let scrollSpy  = Scroll.scrollSpy;



class Navigation extends React.Component {
  render() {
    return (
      <div className={s.root} role="navigation">
        <div className={s.navigationItem}>
            <Link className={s.link} activeClass="active" to="role" spy={true} smooth={true} offset={-50} duration={500} onSetActive={this.handleSetActive}>
                <div className={s.square}></div>
                <span>ROLE</span>
            </Link>
        </div>
        <div className={s.navigationItem}>
            <Link className={s.link} activeClass="active" to="concept" spy={true} smooth={true} offset={0} duration={500} onSetActive={this.handleSetActive}>
                <div className={s.square}></div>
                <span>CONCEPT</span>
                </Link>
        </div>
        <div className={s.navigationItem}>
            <Link className={s.link} activeClass="active" to="issue" spy={true} smooth={true} offset={0} duration={500} onSetActive={this.handleSetActive}>
                <div className={s.square}></div>
                <span>ISSUES</span>
            </Link>
        </div>
        <div className={s.navigationItem}>
            <Link className={s.link} activeClass="active" to="mission" spy={true} smooth={true} offset={0} duration={500} onSetActive={this.handleSetActive}>
                <div className={s.square}></div>
                <span>MISSION</span>
            </Link>
        </div>
        <div className={s.navigationItem}>
            <Link className={s.link} activeClass="active" to="partners" spy={true} smooth={true} offset={0} duration={500} onSetActive={this.handleSetActive}>
                <div className={s.square}></div>
                <span>PARTNERS</span>
            </Link>
        </div>
        <div className={s.navigationItem}>
            <Link className={s.link} activeClass="active" to="team" spy={true} smooth={true} offset={0} duration={500} onSetActive={this.handleSetActive}>
                <div className={s.square}></div>
                <span>TEAM</span>
            </Link>
        </div>
        <div className={s.navigationItem}>
            <Link className={s.link} activeClass="active" to="contact" spy={true} smooth={true} offset={0} duration={500} onSetActive={this.handleSetActive}>
                <div className={s.square}></div>
                <span>CONTACT</span>
            </Link>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(Navigation);



