import React, {PropTypes} from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Intro.css';
import Countdown from '../Countdown';
import classnames from 'classnames';


import imgIntroUrl from './blockpass-intro-ethereum.png'
import imgKey from './key.png'
import imgChain from './chain.png'
import imgConduit from './conduit.png'
import imgVerification from './verification.png'

class Intro extends React.Component {
  render() {
    return (
      <div className={s.root}>
        <div className={classnames(s.row, s.center_md, s.center_xs)}>
          <div className={classnames(s.col_xs_8)}>
            <div className={s.titleContainer}>
              <img className={s.image} src={imgIntroUrl} srcSet={`${imgIntroUrl} 2x`} alt='BlockPass' />
              <h1 className={s.title}>Passport for a Connected World</h1>
              <p className={s.content}>A blockchain identity protocol for the internet of everything</p>
            </div>
          </div>
        </div>
        <div className={classnames(s.row, s.center_md, s.center_xs)}>
          <div className={classnames(s.col_md_2, s.col_xs_8)}>
            <img className={s.icon} src={imgKey} srcSet={`${imgKey} 2x`} alt='BlockPass' />
            <p>A safe passport for a connected world</p>
          </div>
          <div className={classnames(s.col_md_2, s.col_xs_8)}>
            <img className={s.icon} src={imgChain} srcSet={`${imgChain} 2x`} alt='BlockPass' />
            <p>Blockchain based identity for everything</p>
          </div>
          <div className={classnames(s.col_md_2, s.col_xs_8)}>
            <img className={s.icon} src={imgConduit} srcSet={`${imgConduit} 2x`} alt='BlockPass' />
            <p>A secure conduit between individuals, companies, devices and objects</p>
          </div>
          <div className={classnames(s.col_md_2, s.col_xs_8)}>
            <img className={s.icon} src={imgVerification} srcSet={`${imgVerification} 2x`} alt='BlockPass' />
            <p>A verification oracle for statements made by persons or things</p>
          </div>
        </div>
        {/*
          <div className={s.row}>
          <div className={s.col_sm}>
            <Countdown bgcolor='#ffffff' dueDate={1498867200000} title='Countdown'/>
          </div>
        </div>
        */}
      </div>
    );
  }
}

export default withStyles(s)(Intro);



