import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Role.css';
import classnames from 'classnames';
import imgAnimation from './animation.png';
import Anchor from '../Anchor'

class Role extends React.Component {
  static propTypes = {
    title: PropTypes.string.isRequired,
    bgcolor: PropTypes.string.isRequired,
    textcolor: PropTypes.string.isRequired,
  }

  render() {
    return (
        <div style={{backgroundColor: this.props.bgcolor}} className={s.root}>
            <Anchor name={'role'}/>
            <div className={s.section}>
                <div className={classnames(s.row, s.center_xs)}>
                    <div className={s.col_xs_8}>
                        <div className={s.square}></div>
                        <h3 className={s.title}>{ this.props.title }</h3>
                    </div>
                </div>
                <div className={classnames(s.row, s.center_xs)}>
                    <div className={s.col_xs_8}>
                        <img className={s.image} src={imgAnimation} srcSet={`${imgAnimation} 2x`} alt='BlockPass' />
                    </div>
                </div>
            </div>
        </div>

    );
  }
}

export default withStyles(s)(Role);



